// var util = require('../../../common/util.js')
import {mapState,mapMutations} from 'vuex';
export default {
	data() {
		return {
			account: '',
			password:''
		}
	},
	onLoad() {

	},
	methods: {
		...mapMutations(['login']),
		bindLogin(){
			if(!this.account){
				uni.showToast({
					icon: 'none',
					title: '请填写会员账号或手机号',
				});
				this.account = '';
				return;
			}
			if(!this.password){
				uni.showToast({
					icon: 'none',
					title: '请填写登录密码',
				});
				this.password = '';
				return;
			}
			uni.request({
				url:this.websiteUrl + 'auth',
				method:'POST',
				data:{
					phone:this.account,
					password:this.password
				},
				dataType:'json',//服务器返回json格式数据
				header:{'Content-Type':'application/json'},
				success:res=>{
					console.log("登录："+JSON.stringify(res))
					var data = res.data;
					//服务器返回响应，根据响应结果，分析是否登录成功；
					var that = this;
					if(data.code == 200){
						var token = data.data.token;
						console.log("登录成功数据"+JSON.stringify(data));
						this.customMethods.createState(this.account,function(){
							that.customMethods.setState(data.data);
							var state = that.customMethods.getState();
							console.log("登录成功数据"+JSON.stringify(state));
							// that.toLogin(that.account,token);
						})
						that.toLogin(that.account,token);
						this.loadRankNames(token);
					}else if(data.code == 401){
						uni.showToast({
							icon: 'none',
							title: '登录密码错误',
						});
					}else if(data.code == 404){
						uni.showToast({
							icon: 'none',
							title: '用户不存在',
						});
					}else {
						uni.showToast({
							icon: 'none',
							title: data.message,
						});
					}
				},
				fail: () => {},
				complete: () => {}
			})
		},
		loadRankNames(token){
			uni.request({
				url: this.websiteUrl + 'finance/get_rank_list',
				method: 'POST',
				data:{},
				dataType:'json',//服务器返回json格式数据
				header:{'content-type':'application/json','Token':token},
				success: res => {
					var res = res.data;
					if(res.code == 200){
						console.log("获取rank-list成功："+JSON.stringify(res));
						let settings = this.customMethods.getSettings();
						settings.rankNames = res.data;
						this.customMethods.setSettings(settings);
					}
				},
				fail: () => {},
				complete: () => {}
			});
		},
		toLogin(username,token){
			console.log("login_phone.html:toMain");
			var that = this;
			var userName = this.account;
			this.login({userName,token})
			uni.showToast({
				icon: 'success',
				title: '登录成功',
				duration:2000,
				success:function(){
					that.account = '';
					that.password = '';
					setTimeout(()=>{
						uni.reLaunch({
							url: '/pages/tabbar/index/index',
						});
					},1000)
				}
			});
			
		},
		toFindPassword(){
			uni.navigateTo({
				url:"/pages/login/findpassword/findpassword"
			})
		}
	}
}
